@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background-color: #FF9FB2;
  box-shadow: 0px 4.42184px 107.23px #FF9FB2;
  /* background: linear-gradient(92.23deg,
      #da2864 21.43%,
      #ec6091 50.63%,
      #ffabc3 100%,
      #da2864 117.04%);
  box-shadow: 0px 4.42184px 107.23px rgba(255, 86, 246, 0.51); */
}

.btn:hover {
  background: linear-gradient(92.23deg,
      #263D58,
      #3bace2 100%,
      #ec6091 50.63%,
      #da2864 21.43%);
}

.gradient,
.active {
  background: linear-gradient(92.23deg,
      #da2864 21.43%,
      #ec6091 50.63%,
      #ffabc3 100%,
      #da2864 117.04%);
}

.active {
  color: #fff;
  padding: 1rem;
  border-radius: 100%;
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-[#263D58]
  }

  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }

  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }

  .btn {
    @apply rounded-full font-primary text-white font-medium;
  }

  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }

  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }

  .btn-link {
    @apply text-base;
  }

  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#42A6E3] to-[#FF56F6] font-bold font-primary hover:from-[#FF56F6] hover:to-[#42A6E3];
  }

  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}